
























































.alpheios-token-edit-actions-button-container {
  display: inline-block;
  vertical-align: middle;
}
.alpheios-token-edit-actions-button {
  display: inline-block;
  margin: 0 2px;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  border: 1px solid;
  border-radius: 20px;
  cursor: pointer;
  text-align: center;
  line-height: 20px;

  padding: 3px;
  svg {
    display: inline-block;
    width: 100%;
    height: 100%;
    vertical-align: top;
  }
}
