.alpheios-alignment-editor-align-define-container-inner {
  margin-top: 15px;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  background: #F8F8F8;
  color: #ae0000;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data {
  background: #F8F8F8;
  border-bottom: 2px solid #ddd;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data.alpheios-alignment-editor-align-segment-data-last {
  border-bottom-color: transparent;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-text-segment {
  border-bottom: 2px solid #e3e3e3;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-text-segment.alpheios-align-text-segment-origin, .alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-text-segment.alpheios-align-text-segment-target-last {
  border-bottom: 2px solid transparent;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data-item {
  width: 50%;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data-item.alpheios-alignment-editor-align-segment-data-target {
  border-left: 2px solid #ddd;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data {
  display: table;
  width: 100%;
}
.alpheios-alignment-editor-align-define-container-inner .alpheios-alignment-editor-align-segment-data-item {
  display: table-cell;
}