.alpheios-alignment-editor-hidden {
  visibility: hidden;
}

.alpheios-alignment-editor-text-blocks-single textarea {
  width: 100%;
  min-height: 300px;
  font-size: inherit;
}
.alpheios-alignment-editor-text-blocks-single p {
  margin-bottom: 10px;
}
.alpheios-alignment-editor-text-blocks-single p.alpheios-alignment-editor-text-blocks-single__characters {
  color: #888;
  font-size: 90%;
  margin: 0;
}
.alpheios-alignment-editor-text-blocks-single p.alpheios-alignment-editor-red {
  color: #99002a;
}

.alpheios-alignment-editor-text-blocks-single__title {
  position: relative;
}

.alpheios-alignment-editor-text-blocks-single__remove {
  display: inline-block;
  width: 25px;
  height: 25px;
  right: 0;
  top: 50%;
  top: 0;
  position: absolute;
  cursor: pointer;
}
.alpheios-alignment-editor-text-blocks-single__remove svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.alpheios-alignment-editor-options-fieldset {
  padding: 10px;
  border: 2px groove #f8f8f8;
  margin-bottom: 30px;
}
.alpheios-alignment-editor-options-fieldset legend {
  display: block;
  padding: 0 10px;
  margin-bottom: 10px;
  line-height: inherit;
  color: inherit;
  white-space: normal;
  font-size: 110%;
}

.alpheios-alignment-editor-options-fieldset-slim {
  padding: 0;
  margin-bottom: 0;
  border: 0;
}
.alpheios-alignment-editor-options-fieldset-slim legend {
  display: none;
}

.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-option-item__label {
  width: auto;
  margin-right: 20px;
}
.alpheios-alignment-editor-options-fieldset-label-auto input.alpheios-alignment-input,
.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-select,
.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-radio-block {
  width: auto;
}