.alpheios-alignment-editor-actions-menu p.alpheios-editor-content-title {
  margin: 0;
}

.alpheios-editor-content-title {
  font-weight: bold;
  padding: 0 0 5px 0;
  border-bottom: 2px solid #ddd;
}

.alpheios-editor-content-list {
  column-count: 2;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.alpheios-editor-content-list.alpheios-editor-content-list__col2 {
  column-count: 2;
}
.alpheios-editor-content-list.alpheios-editor-content-list__col4 {
  column-count: 4;
}

.alpheios-editor-content-link {
  cursor: pointer;
  display: inline-block;
  width: 100%;
  padding-bottom: 20px;
}
.alpheios-editor-content-link .alpheios-editor-content-link__text {
  text-decoration: underline;
  display: inline-block;
  width: 100%;
}
.alpheios-editor-content-link .alpheios-editor-content-link__checkbox {
  display: inline-block;
  width: 90%;
}

.alpheios-editor-content-breadcrumbs {
  margin: 0 0 10px 0;
  padding: 0;
}
.alpheios-editor-content-breadcrumbs li {
  display: inline-block;
  padding: 5px 5px 5px 0;
}
.alpheios-editor-content-breadcrumbs li:after {
  content: " > ";
}
.alpheios-editor-content-breadcrumbs li:last-child:after {
  content: "";
}
.alpheios-editor-content-breadcrumbs li.alpheios-editor-content-breadcrumbs__link {
  cursor: pointer;
  text-decoration: underline;
  color: #185F6D;
}

.alpheios-editor-content-description p.alpheios-editor-content-description__title {
  text-decoration: underline;
  color: #185F6D;
  margin: 0 0 10px 0;
  cursor: pointer;
}
.alpheios-editor-content-description .alpheios-editor-content-description__details {
  margin: 0 0 10px;
  padding: 0 20px 0;
  border-bottom: 2px solid #ddd;
}