.alpheios-alignment-editor-metadata-item {
  margin-bottom: 10px;
}
.alpheios-alignment-editor-metadata-item p > label {
  width: 38%;
  display: inline-block;
  vertical-align: top;
  text-transform: capitalize;
}
.alpheios-alignment-editor-metadata-item p > label .alpheios-alignment-editor-metadata-item__label-help {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: #185F6D;
  color: #fff;
  border: 1px solid #185F6D;
  border-radius: 20px;
  text-align: center;
  line-height: 20px;
  margin-left: 5px;
}
.alpheios-alignment-editor-metadata-item p > input.alpheios-alignment-editor-metadata-item__control {
  display: inline-block;
  width: 60%;
  vertical-align: top;
}
.alpheios-alignment-editor-metadata-item .alpheios-alignment-editor-metadata-item__allvalues {
  padding-left: 30%;
}
.alpheios-alignment-editor-metadata-item .alpheios-alignment-editor-metadata-item__allvalues-item {
  display: inline-block;
  vertical-align: baseline;
  cursor: pointer;
  text-decoration: underline;
  padding: 0 7px 0 0;
}
.alpheios-alignment-editor-metadata-item .alpheios-alignment-editor-metadata-item__input-value {
  position: relative;
  padding-right: 30px;
}
.alpheios-alignment-editor-metadata-item .alpheios-alignment-editor-metadata-item__remove {
  display: inline-block;
  width: 25px;
  height: 25px;
  right: 0;
  top: 50%;
  top: 0;
  position: absolute;
  cursor: pointer;
}
.alpheios-alignment-editor-metadata-item .alpheios-alignment-editor-metadata-item__remove svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}