.alpheios-alignment-editor-tokens-edit-editor-container-inner {
  margin-top: 15px;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  background: #F8F8F8;
  color: #ae0000;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-segment-data {
  display: table;
  width: 100%;
  background: #F8F8F8;
  border-bottom: 2px solid #ddd;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-segment-data.alpheios-alignment-editor-align-segment-data-last {
  border-bottom-color: transparent;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-text-segment-edit {
  border-bottom: 2px solid #e3e3e3;
  padding: 10px;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-text-segment-edit.alpheios-align-text-segment-origin, .alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-text-segment-edit.alpheios-align-text-segment-target-last {
  border-bottom: 2px solid transparent;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-segment-edit-data-item {
  width: 50%;
  display: table-cell;
}
.alpheios-alignment-editor-tokens-edit-editor-container-inner .alpheios-alignment-editor-align-segment-edit-data-item.alpheios-alignment-editor-align-segment-data-target {
  border-left: 2px solid #ddd;
}