.alpheios-alignment-options-block .alpheios-alignment-editor-container {
  border-bottom: 5px solid #f8f8f8;
  border-top: 5px solid #f8f8f8;
}

.alpheios-alignment-options__group {
  padding: 10px;
  border: 2px groove threedface;
  margin-bottom: 30px;
}
.alpheios-alignment-options__group legend {
  display: block;
  padding: 0 10px;
  margin-bottom: 10px;
  line-height: inherit;
  color: inherit;
  white-space: normal;
  font-size: 110%;
  font-weight: bold;
}

.alpheios-alignment-options__buttons {
  padding: 5px 0 15px;
  text-align: left;
}

.alpheios-alignment-options__aboutcont {
  border-top: 1px solid #f8f8f8;
  padding: 10px 0;
  font-size: 12px;
}
.alpheios-alignment-options__aboutcont h3 {
  margin: 0;
}