  max-height: 400px;
  overflow-y: scroll;
  padding: 10px;
  position: relative;

  .alpheios-alignment-editor-align-text-segment__up-down {
    position: sticky;
    bottom: -15px;
    left: 0; right: 0;
    width: 100%;
    // background: #F8F8F8;
    background: transparent;
    text-align: right;
    padding: 5px;

    .alpheios-align-text-segment-button {
      display: inline-block;
      padding: 2px;
      height: 30px;
      width: 30px;
      cursor: pointer;
      border-radius: 10px;

        svg {
          display: inline-block;
          width: 100%;
          height: 100%;
          vertical-align: top;
        }
    }
  }
}
